%flex-row {
  display: flex;
  flex-direction: row;
}

%flex-col {
  display: flex;
  flex-direction: column;
}

%justify-center {
  justify-content: center;
}

%justify-between {
  justify-content: space-between;
}

%align-center {
  align-items: center;
}

%self-end {
  align-self: flex-end;
}

%wrap {
  flex-wrap: wrap;
}
